.main{
    height: 40vh;
    background-color: rgba(135, 185, 250, 0.44);
}
.container{
    flex-direction: column;
    justify-content: space-evenly;
}
.container h2{
    background-color: rgba(238, 155, 177, 0.73);
}
.row{
    width: 50%;
    border: 1px solid white;
    display: flex;
    justify-content: space-between;
}

.img{
    height: 80px;
    width: 80px;
}

.p{
    font-weight: bold;
    margin: 0 auto;
    background-color: white;
    width:40%;
}
.p p{
    margin: 0;
}
@media (max-width:550px){
.row{
    width: 100%;
}
}
