.main{
    min-height: 100vh;
    background-color: rgba(250, 135, 170, 0.44);
}
.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.container h2{
    background-color: rgba(155, 209, 238, 0.73)
}
.form{
    width: 50%;
    margin: 2% auto;
    border: 1px solid white;
}
.formInput{
    width: 98%;
    margin-bottom: 3% ;
}
.formArea{
    margin-bottom: 0 ;
}
.formSubmit{
    background: white;
    border: 1px solid white;
    padding: 5px;
    width: 95px;
}
@media(max-width:550px){
    .form {
        width: 90%;
    }
}
